<template>
  <div class="bg">
    <div class="wall">
      <PhotoFrame
        class="photo"
        v-for="(v, i) in list"
        :key="i"
        :url="v"
        :desc="'No.' + i"
      ></PhotoFrame>
    </div>
  </div>
</template>
<script>
import PhotoFrame from "@/components/PhotoFrame.vue";
import { loadFrames } from "@/assets/js/utils";
export default {
  components: {
    PhotoFrame,
  },
  data() {
    return {
      list: [],
    };
  },
  mounted() {
    const context = require.context(
      "@/assets/images",
      false,
      /[.jpg | .png | .gif]/
    );
    const arr = loadFrames(context);
    this.list = arr;
  },
};
</script>
<style lang="less" scoped>
@import url("~@/assets/css/color.less");
.bg {
  height: 100%;
  background: @green;
  .wall {
    // max-width: 1200px;
    margin: auto;
    display: flex;
    // justify-content: space-around;
    flex-wrap: wrap;
    .photo {
      margin: 20px 40px;
    }
  }
}
</style>