
function drag(id) {
  if(!id) return;
  const el = document.getElementById(id);
  let lock = false,
      top = 0,
      left = 0,
      startX = 0,
      startY = 0;
  el.style.position = "fixed";
  el.style.top = 0;
  el.style.left = 0;
  console.log("元素", el.onClick, el);
  el.addEventListener("mousedown", (e) => {
    startX = e.clientX;
    startY = e.clientY;
    document.onmousemove = (ev) => {
      top += ev.movementY;
      left += ev.movementX;
      el.style.top = top + "px";
      el.style.left = left + "px";
    }
  })
  el.addEventListener("mouseup", (e) => {
    lock = !(startX === e.clientX && startY === e.clientY);
    document.onmousemove = null;
  })
  return lock;
}

function getPageAllImage() {
  // 获取页面所有图片
  const imgs = document.getElementsByTagName("img");
  const pics = Array.apply(null, imgs); // HTMLCollection类型转数组 (why?)
  const urls = pics.map(v => v.src);
  return urls;
}

const weeks = [
  '星期日',
  '星期一',
  '星期二',
  '星期三',
  '星期四',
  '星期五',
  '星期六',
]
function getWeek(sub) { // 星期
  return weeks[sub];
}


const meridians = [
  [0, 6, '凌晨'],
  [6, 9, '清晨'],
  [9, 11, '上午'],
  [11, 13, '中午'],
  [13, 17, '下午'],
  [17, 19, '傍晚'],
  [19, 21, '夜晚'],
  [21, 24, '深夜'],
];
function getMeridian(sub) { // 子午
  let result = '';
  for(let i = 0; i < meridians.length; i++) {
    if(sub >= meridians[i][0] && sub < meridians[i][1]) {
      result = meridians[i][2];
      break;
    }
  }
  return result;
}

/**
 * 
 * @param {*} context require.context 创建的函数
 * @returns 返回资源数组
 */
export function loadFrames(context) { // 批量加载资源
  const frames = [];
  context.keys().forEach((k) => {
    frames.push(context(k));
  });
  return frames;
}

/**
 * 防抖
 * @param {Boject} func 执行的方法
 * @param {Number} wait 延迟时间
 * @param {Boolean} immediate 是否利息执行
 *
 * 使用方法 debounce: debounce(function () { ... todo})
 * 不可使用箭头韩式 () => {... todo} 会丢失this
 */
export function debounce(func, wait = 1000, immediate = true) {
	let timer;
	return function() {
		timer && clearTimeout(timer);
		if (immediate) {
			let once = !timer;
			timer = setTimeout(() => {
				timer = null;
			}, wait);
			once && func.apply(this, arguments);
		} else {
			timer = setTimeout(() => {
				func.apply(this, arguments);
			}, wait);
		}
	};
}

